
.input-custom {
    font-weight: 600;
}

.uppercase {
  text-transform: uppercase;
}

.day-custom {
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 600;
}

.input-custom:focus {
  border-color: #d3d3d3; /* Светло-серый цвет */
  box-shadow: 0 0 0 0.2rem rgba(211, 211, 211, 0.5);
}


.input-custom::placeholder {
  color: #b5b5b5; /* Светло-серый цвет */
  opacity: 1; /* Убедитесь, что он полностью видим */
}

.multi-select-list {
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  cursor: pointer;
}

.list-item {
  padding: 8px;
  margin-bottom: 5px;
  border: 1px solid transparent;
  transition: background 0.3s;
}

.list-item:hover {
  background: #e0e0e0;
  color: black;
}

.list-item.selected {
  background: #8a2be2; /* пурпурный */
  color: white;
  border-color: #5b1f8d; /* темно-пурпурный */
}

.border-custom {
  border-color: #8a2be2 !important;
}

button.border-custom-purple {
    border-color: #8a2be2 !important;
    color: #fff !important;
}

button.border-custom-purple:hover {
    background-color: #8a2be2 !important;
    color: #fff !important;
}

button.border-custom-mint {
    border-color: #66D9D9 !important;
    color: #66D9D9 !important;
}

button.border-custom-mint:hover {
    background-color: #66D9D9 !important;
    color: #fff !important;
}
